import React, { Component } from "react";

import { searchImgs } from "../../assets/data";
import "./SearchTags.css";

class SearchTags extends Component {
  constructor(props) {
    super(props);

    this.state = {
      blink: "|",
    };
  }

  componentDidMount() {
    this.intervalID = setInterval(() => this.tick(), 1000);
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  tick() {
    let newBlink = this.state.blink === "|" ? "" : "|";
    this.setState({
      blink: newBlink,
    });
  }

  render() {
    return (
      <div className="SearchTags">
        <div className="row d-flex align-items-center top">
          <div className="col-10 p-0 heading">
            google search{this.state.blink}
          </div>
          <div className="col-1 pl-0">
            <img src={searchImgs.mic} alt="mic" className="mic" />
          </div>
          <div className="col-1 pl-0">
            <img src={searchImgs.search} alt="search" />
          </div>
        </div>
        <div className="bottom">
          <div>
            favorites <strong>animal crossing</strong>
          </div>
          <div>
            anime <strong>manhwa/manga</strong>
          </div>
          <div>
            fashion <strong>earrings</strong>
          </div>
        </div>
      </div>
    );
  }
}

export default SearchTags;
