import { act } from "react-dom/test-utils";
import { SET_COLOR } from "../actions/types";

const initialState = {
  color: "#dec6c6",
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_COLOR:
      return {
        ...state,
        color: action.color,
      };
    default:
      return state;
  }
};

export default reducer;
