import React from "react";

import "./Heading.css";

const Heading = (props) => {
  return (
    <div className={`Heading ${props.inplace ? "inplace" : ""}`}>
      <span
        className={`${
          props.reverse
            ? `font-accent accent ${props.large ? "large" : ""}`
            : "font-cursive cursive"
        }`}
      >
        {props.first}
      </span>
      <span
        className={`${
          props.reverse
            ? "font-cursive cursive"
            : `font-accent accent ${props.large ? "large" : ""}`
        }`}
      >
        {props.second}
      </span>
    </div>
  );
};

export default Heading;
