import React from "react";
import Heading from "../../common/Heading";
import { skillsInfo } from "../../../assets/data";

import "./Skills.css";

const Skills = (props) => {
  return (
    <div className="Skills font-primary">
      {props.header ? <Heading first="skills" second="set." /> : null}
      <div className="wrapper">
        {Object.keys(skillsInfo).map((skillType, indx) => {
          let subHeading = "";
          if (indx === 0) {
            subHeading = (
              <div className="sub-heading col-12 pb-2">
                lan<span className="font-accent">guages</span>
              </div>
            );
          } else if (indx === 1) {
            subHeading = (
              <div className="sub-heading col-12 pb-2">
                frameworks /<span className="font-accent"> libraries</span>
              </div>
            );
          } else {
            subHeading = (
              <div className="sub-heading col-12 pb-2">
                technical <span className="font-accent">tools</span>
              </div>
            );
          }

          return (
            <div className="row skills" key={`skills-${indx}`}>
              {subHeading}
              <div className="col-1 p-0"> </div>
              <div className="col-11 pl-0">
                {skillsInfo[skillType].map((data, indx, array) => {
                  return (
                    <div key={`skill-${indx}`} style={{ display: "inline" }}>
                      {indx !== 0 && indx !== array.length ? (
                        <span className="line"> | </span>
                      ) : null}
                      {data}
                    </div>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Skills;
