import React from "react";

import { infoBox } from "../../assets/data";
import "./InfoBox.css";

const InfoBox = () => {

  return (
    <div className="InfoBox">
      <div className="face-picture d-flex justify-content-center">
        <img
          src={infoBox.imageURL}
          alt="my face"
          style={{ filter: "opacity(0.8)", maxWidth: 100, maxHeight: 75 }}
        />
      </div>
      <p className="mx-auto font-primary">{infoBox.text}</p>
    </div>
  );
};

export default InfoBox;
