import React, { Component } from "react";

import { catImgs, imageIcons, imageURLs } from "../../assets/data";
import { infoBox } from "../../assets/data";
import "./ImageCarousel.css";

class ImageCarousel extends Component {
  constructor(props) {
    super(props);

    let username = ["miso", "soba", "tofu"];
    let randUser = Math.floor(Math.random() * 3);

    this.state = {
      currentImg: 0,
      username: username[randUser],
    };
  }

  changePicture = () => {
    let el = document.getElementById(`dot${this.state.currentImg}`);
    el.classList.remove("selected");
    let nextImg = (this.state.currentImg + 1) % 4;
    el = document.getElementById(`dot${nextImg}`);
    el.classList.add("selected");
    this.setState({ currentImg: nextImg });
  };

  render() {
    let cycleImgs = [catImgs.all, catImgs.miso, catImgs.soba, catImgs.tofu];
    return (
      <div className="ImageCarousel mt-4">
        <div className="heading d-flex align-items-center">
          <img
            className="pfp"
            src={infoBox.imageURL2}
            alt="pfp"
            style={{
              filter: "opacity(0.8) grayscale(0.5)",
              maxWidth: 25,
              maxHeight: 25,
              borderRadius: 13,
            }}
          />
          <div className="pl-2">
            tnwi.png <span className="ellipses">...</span>
          </div>
        </div>
        <div className="d-flex justify-content-center">
          <img
            className="images"
            src={cycleImgs[this.state.currentImg]}
            alt="cat pics"
            onClick={this.changePicture}
          />
        </div>
        <div className="d-flex align-items-center row py-1">
          <div className="col-4 pl-3 icons">
            <img src={imageIcons.heart} alt="heart" />
            <img src={imageIcons.chat} alt="chat" />
            <img src={imageIcons.send} alt="send" />
          </div>

          <div className="d-flex col-4 justify-content-center">
            <div className="dot selected" id="dot0"></div>
            <div className="dot" id="dot1"></div>
            <div className="dot" id="dot2"></div>
            <div className="dot" id="dot3"></div>
          </div>
          <div className="col-4 d-flex justify-content-end pr-3">
            <img src={imageIcons.bookmark} alt="bookmark" />
          </div>
        </div>
        <div className="px-3 pb-3">
          <div className="d-flex align-items-center pb-1">
            <img
              src={catImgs.miso2}
              alt="miso"
              style={{
                filter: "opacity(0.8)",
                maxWidth: 15,
                maxHeight: 15,
                borderRadius: 8,
              }}
            />
            <div className="px-2">
              Liked by <strong>{this.state.username}</strong> and{" "}
              <strong>others</strong>
            </div>
          </div>
          <div className="caption pb-1">
            <strong>tnwi.png</strong> my cats - miso the bengal | soba the
            tuxedo | and tofu the baby
          </div>
          <div className="comments">View all 18 comments</div>
        </div>
      </div>
    );
  }
}

export default ImageCarousel;
