import React, { Component } from "react";

import "./Navbar.css";

import { Link } from "react-router-dom";
class Navbar extends Component {
  scrollToTop = () => {
    document.getElementById("scrollable").scrollTop = 0;
  };

  render() {
    return (
      <div className="Navbar font-secondary">
        <Link to="/" onClick={this.scrollToTop}>
          <mark> Home</mark>{" "}
        </Link>
        /
        <Link to="/projects" onClick={this.scrollToTop}>
          <mark> Projects</mark>{" "}
        </Link>
        /
        <Link to="/resume">
          <mark> Resume</mark>{" "}
        </Link>
        /
        <Link to="/about">
          <mark> About</mark>{" "}
        </Link>
      </div>
    );
  }
}

export default Navbar;
