import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { resumeLink } from "../../assets/data";
import "./ResumeViewer.css";

class ResumeViewer extends Component {
  state = {
    numPages: null,
    pageNumber: 1,
  };

  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  };

  render() {
    return (
      <div className="ResumeViewer d-flex align-items-start">
        <div className="font-primary link-wrapper">
          <Link to="/">
            <mark>⇐ Go Back</mark>
          </Link>
        </div>
        <div className="d-flex justify-content-center align-items-center font-primary m-auto pt-2 flex-column">
          <Document
            className="document"
            file={resumeLink}
            onLoadSuccess={this.onDocumentLoadSuccess}
          >
            <Page pageNumber={this.state.numPages} />
          </Document>

          <div className="mt-3">
            <a
              className="link-wrapper"
              href="https://twpeng-portfolio.s3-us-west-1.amazonaws.com/pdf/TenwiePeng+Resume.pdf"
            >
              <mark>Download</mark>
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default ResumeViewer;
