import React from "react";
import Heading from "../../common/Heading";

import "./ProfessionalClass.css";

const ProfessionalClass = (props) => {
  return (
    <div className="ProfessionalClass font-primary">
      {props.header ? (
        <Heading first="professional" second="development." />
      ) : null}
      <div className="wrapper">
        <div className="sub-heading">
          online <span className="font-accent">courses</span>
        </div>
        {props.data.map((onlineCourse, indx, array) => {
          return (
            <div
              className={`row courses ${
                indx === array.length - 1 ? null : "border-bottom"
              }`}
              key={`course-${indx}`}
            >
              <div className="col-12 px-0 title">
                ‣ {onlineCourse.name} 
              </div>
                <div className="col-6 py-2 instructor"> {onlineCourse.instructor}</div>
              <div className="col-6 py-2 px-0 date">{onlineCourse.date}</div>
              <div className="col-1 p-0"></div>
              <div className="col-11 pl-0 description">
                {onlineCourse.description.map((data, indx) => {
                  return (
                    <div className="row pb-2" key={`descrip-${indx}`}>
                      <div className="col-1 pr-0">•</div>
                      <div className="pl-0 col-11">{data}</div>
                    </div>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default ProfessionalClass;
