import React, { Component } from "react";

import SidePanel from "../components/home/SidePanel";
import ContentContainer from "../components/home/ContentContainer";

class Home extends Component {
  state = {
    route: null,
  };
  componentDidMount() {
    // this.setState({page:this.props.page})
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.page !== this.props.page) {
      if (this.props.page === "/links") {
      } else {
        this.setState({ route: this.props.page });
      }
    }
  }

  render() {
    return (
      <div
        className="Home align-items-center d-none d-lg-flex"
        style={{ height: "100%" }}
      >
        <div
          className="col-7 pr-0 d-flex justify-content-end"
          style={{ height: "67.5%", minHeight: 690 }}
        >
          <ContentContainer type={this.state.route} />
        </div>
        <div
          className="col-5 pl-0"
          style={{ height: "67.5%", minHeight: 690, left: -1 }}
        >
          <SidePanel />
        </div>
      </div>
    );
  }
}

export default Home;
