import React from "react";
import Heading from "../../common/Heading";

import "./University.css";

const University = (props) => {
  return (
    <div className="University font-primary">
      {props.header ? <Heading first="university" second="courses." /> : null}
      <div className="wrapper">
        <div className="sub-heading">
          relevent <span className="font-accent">coursework.</span>
        </div>
        <div className="row school-courses">
          <div className="col-12 px-0 title">‣ {props.data.university}</div>
          <div className="col-12 px-0">
            <div className="row pb-2">
              <div className="col-1 p-0"></div>
              <div className="col-11 p-0">
                {props.data.courses.map((data, indx) => {
                  return (
                    <div className="row courses" key={`course-${indx}`}>
                      <div className="col-1 pr-0">•</div>
                      <div className="col-11 p-0">{data}</div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default University;
