import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import Buttons from "../common/Buttons";
import Heading from "../common/Heading";
import Modal from "./Modal";

import { links } from "../../assets/data";
import "./Links.css"

class Links extends Component {
  closeModal = () => {
    this.props.history.goBack();
  };

  render() {
    let linkContent = links.map((data, indx) => {
      return (
        <div className="d-flex align-items-center" key={`link-${indx}`}>
          <img
            src={data.icon}
            alt={data.title}
            style={{ filter: "opacity(0.4)" }}
          />{" "}
          <div className="pl-2 pb-1">
          <a className="link" href={data.title !=="discord" ? data.url :null}>{data.name}</a></div>
        </div>
      );
    });

    return (
      <div className="Links">
        <Modal
          show={true}
          modalClosed={() => {
            this.closeModal();
          }}
        >
          <div className="d-flex justify-content-end">
            <Buttons
              xHandler={() => {
                this.closeModal();
              }}
            />
          </div>
          <Heading inplace first="links" />
          <div className="p-3">{linkContent}</div>
        </Modal>
      </div>
    );
  }
}

export default withRouter(Links);
