import React from "react";

import University from "./widgets/University";
import { universityInfo } from "../../assets/data";

const UniversityList = () => {
  return (
    <div>
      {universityInfo.map((data, indx) => {
        if (indx === 0) {
          return (
            <University header data={data} key={`uni-${indx}`} />
          );
        } else {
          return <University data={data} key={`uni-${indx}`} />;
        }
      })}
    </div>
  );
};

export default UniversityList;
