import React from "react";

import Projects from "./widgets/Projects";
import { projectsInfo } from "../../assets/data";

const ProjectsList = () => {
  return (
    <div className="pb-4" >
      {projectsInfo.map((data, indx) => {
        if (indx === 0) {
          return (
            <Projects
              header
              name={data.name}
              image={data.imageURL}
              link={data.link}
              keywords={data.keywords}
              description={data.description}
              key={`project-${indx}`}
            />
          );
        } else {
          return (
            <Projects
              name={data.name}
              image={data.imageURL}
              link={data.link}
              keywords={data.keywords}
              description={data.description}
              key={`project-${indx}`}
            />
          );
        }
      })}
    </div>
  );
};

export default ProjectsList;
