import React, { Component } from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import Home from "./pages/Home";
import About from "./pages/About";
import Blog from "./pages/Blog";
import Resume from "./pages/Resume";
import Links from "./components/modals/Links";

import "./App.css";

class App extends Component {
  
  render() {
    return (
      <div className="App">
        <BrowserRouter basename="https://tenwiepeng.github.io/">
          {/* <div style={{position:"absolute", border:"1px solid black"}}>Music</div> */}
          <Switch>
            <Route exact path="/about" component={About} />
            <Route exact path="/acnh" component={Blog} />
            <Route path="/resume" component={Resume} />
            <Redirect to="/" />
          </Switch>

          <Route
            path="/message"
            component={() => (
              <div style={{ position: "absolute" }}>message</div>
            )}
          />
          <Route
            path="/links"
            component={() => (
              <div
                style={{ position: "absolute", width: "100%", height: "100%" }}
              >
                <Links />
              </div>
            )}
          />

          <Route
            path="/"
            component={({ location, history }) => {
              if (
                location.pathname === "/" ||
                location.pathname === "/message" ||
                location.pathname === "/links" ||
                location.pathname === "/projects"
              ) {
                return <Home page={location.pathname} />;
              } else {
                return null;
              }
            }}
          />
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
