// name={data.name}
// image={data.imageURL}
// link={data.link}
// keywords={data.keywords}
// description={data.description}
import React from "react";
import Heading from "../../common/Heading";

import "./Projects.css";

const Projects = (props) => {
  let link = null;
  if (props.link) {
    link = (
      <span className="link">
        {" "}
        | <a href={props.link}>link</a>
      </span>
    );
  }

  return (
    <div className="Projects">
      {props.header ? <Heading first="personal" second="projects." /> : null}
      <div className="wrapper">
        <div className=" image-wrapper d-flex justify-content-center">
          <img src={props.image} alt={props.name} />
        </div>
        <div className="name-info font-primary">{props.name} {link}</div>
        <div className="keywords font-primary">{props.keywords}</div>
        <div className="description font-primary">
          {props.description.map((data, indx) => {
            return (
              <div className="row pb-2" key={indx}>
                <div className="col-1 p-0">‣</div>
                <div className="pl-0 col-11">{data}</div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Projects;
