import React from "react";

import ProfessionalClass from "./widgets/ProfessionalClass";
import { developmentInfo } from "../../assets/data";

const ProfessionalList = () => {
  return (
    <div>
      {developmentInfo.certs === null ? (
        <ProfessionalClass
          header
          data={developmentInfo.onlineCourses}
          key={`prof-1`}
        />
      ) : null}
    </div>
  );
};

export default ProfessionalList;
