import React from "react";

import Navbar from "./Navbar";
import Deco from "./Deco";
import Clock from "../home/Clock";
import CircleLink from "./CircleLink";
import InfoBox from "./InfoBox";

import { homeLinks } from "../../assets/data";
import "./SidePanel.css";
import InputColor from "./InputColor";

const SidePanel = (props) => {
  let circleLinks = homeLinks.map((link) => {
    return <CircleLink key={link.title} to={link.url} titleText={link.title} />;
  });

  return (
    <div className="SidePanel">
      <div className="navbar">
        <Navbar />
      </div>
      <div className="rectangle-1"></div>
      <div className="font-accent greeting">hello and welcome.</div>
      <div className="rectangle-2">
        <Deco />
      </div>
      <div className="rectangle-3">
        <InfoBox />
      </div>
      <div className="rectangle-4"></div>
      <div className="circlelink">{circleLinks}</div>
      <div id="color-input">
        <InputColor />
      </div>
      <div className="clock">
        <Clock />
      </div>
    </div>
  );
};

export default SidePanel;
