import React from "react";

import Work from "./widgets/Work";
import { workInfo } from "../../assets/data";

const WorkList = () => {
  return (
    <div>
      {workInfo.map((data, indx) => {
        if (indx === 0) {
          return (
            <Work
              header
              company={data.company}
              image={data.imageURL}
              position={data.position}
              date={data.date}
              location={data.location}
              keywords={data.keywords}
              description={data.description}
              key={`work-${indx}`}
            />
          );
        } else {
          return (
            <Work
              company={data.company}
              image={data.imageURL}
              position={data.position}
              date={data.date}
              location={data.location}
              keywords={data.keywords}
              description={data.description}
              key={`work-${indx}`}
            />
          );
        }
      })}
    </div>
  );
};

export default WorkList;
