import React, { Component } from "react";

import "./Clock.css"

class Clock extends Component {
  constructor(props) {
    super(props);
    let current_date = new Date();
    let formated_date = this.formatDate(current_date);
    this.state = {
      time: formated_date,
      amOrPm: current_date.getHours() < 12 ? " AM" : " PM"
    };
  }
  componentDidMount() {
    this.intervalID = setInterval(() => this.tick(), 1000);
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  tick() {
    let current_date = new Date();
    let formated_date = this.formatDate(current_date);
    this.setState({
      time: formated_date,
      amOrPm: current_date.getHours() < 12 ? " AM" : " PM"
    });
  }

  formatDate(date) {
    let hour = date.getHours();
    hour = hour > 12 ? hour - 12 : hour;
    hour = hour === 0 ? hour + 12 : hour;

    let min =
      date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
    let sec =
      date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();

    return (
      date.getMonth() +
      1 +
      "." +
      date.getDate() +
      "." +
      date.getFullYear() +
      " | " +
      +hour +
      "." +
      min +
      "." +
      sec
    );
  }
  render() {
  return <p className="Clock font-primary"><span className="font-mono">{this.state.time}</span>{this.state.amOrPm}</p>;
  }
}

export default Clock;
