import React, { Component } from "react";

import "./InputColor.css";

class InputColor extends Component {
  constructor() {
    super();
    this.state = {
      color: localStorage.getItem("primaryColor") || "#dec6c6",
      key: localStorage.getItem("primaryColor") || "#dec6c6",
    };
  }

  componentDidMount() {
    this.initColor();
    window.addEventListener(
        "beforeunload", ()=>{
        localStorage.removeItem("primaryColor")
        this.setState({color: "#dec6c6"})}
      );
  }

  initColor() {
    let red = Number(
      window
        .getComputedStyle(document.documentElement)
        .getPropertyValue("--red")
    );
    let green = Number(
      window
        .getComputedStyle(document.documentElement)
        .getPropertyValue("--green")
    );
    let blue = Number(
      window
        .getComputedStyle(document.documentElement)
        .getPropertyValue("--blue")
    );

    let color = this.rgbToHex(red, green, blue);
    this.setState({ color: color });
  }

  rgbaToRgb(r, g, b, a, r2, g2, b2) {
    var r3 = Math.round((1 - a) * r2 + a * r);
    var g3 = Math.round((1 - a) * g2 + a * g);
    var b3 = Math.round((1 - a) * b2 + a * b);
    return r3 + "," + g3 + "," + b3;
  }

  handleColor = () => {
    let color = this.hexToRgb(document.getElementById("color-picker").value);

    this.setState({ color: document.getElementById("color-picker").value });
    localStorage.setItem(
      "primaryColor",
      document.getElementById("color-picker").value
    );

    document.documentElement.style.setProperty("--red", color.r);
    document.documentElement.style.setProperty("--green", color.g);
    document.documentElement.style.setProperty("--blue", color.b);

    let backgroundColor = this.rgbaToRgb(
      color.r,
      color.g,
      color.b,
      0.6,
      255,
      255,
      255
    );
    document.documentElement.style.setProperty(
      "--backgroundColor",
      backgroundColor
    );

    let color1 = color;
    color1.r = color1.r + 10 < 256 ? color1.r + 10 : 255;
    color1.g = color1.g - 20 < 0 ? 0 : color1.g - 20;
    color1.b = color1.b + 10 < 256 ? color1.b + 10 : 255;

    document.documentElement.style.setProperty(
      "--patternColor1",
      color1.r + ", " + color1.g + ", " + color1.b
    );

    let color2 = color;
    color2.r = color2.r - 80 < 0 ? 0 : color2.r - 80;
    color2.g = color2.g - 10 < 0 ? 0 : color2.g - 10;
    color2.b = color2.b + 40 < 256 ? color2.b + 40 : 255;

    document.documentElement.style.setProperty(
      "--patternColor2",
      color2.r + ", " + color2.g + ", " + color2.b
    );
  };

  rgbToHex(r, g, b) {
    return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
  }

  hexToRgb(hex) {
    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result
      ? {
          r: parseInt(result[1], 16),
          g: parseInt(result[2], 16),
          b: parseInt(result[3], 16),
        }
      : null;
  }

  render() {
    return (
      <input
        id="color-picker"
        type="color"
        defaultValue={this.state.color}
        onChange={this.handleColor}
      />
    );
  }
}

export default InputColor;
