import React from "react";
import Heading from "../../common/Heading";

import "./Work.css";

const Work = (props) => {
  return (
    <div className="Work">
      {props.header ? (
        <Heading first="work" second="experience."/>
      ) : null}
      <div className="wrapper">
        <div className=" image-wrapper d-flex justify-content-center">
          <img src={props.image} alt={props.company} />
        </div>
        <div className="company-info font-primary">
          {props.company}{" "}
          <span className="position font-accent">{props.position}</span>
        </div>
        <div className="keywords font-primary">{props.keywords}</div>
        <div className="description font-primary">
          {props.description.map((data, indx) => {
            return (
              <div className="row pb-2" key={indx}>
                <div className="col-1 p-0">‣</div>
                <div className="pl-0 col-11">{data}</div>
              </div>
            );
          })}
        </div>

        <div className="date font-primary">
          {props.date} <span className="line">|</span>{" "}
          <span className="location"> {props.location}</span>
        </div>
      </div>
    </div>
  );
};

export default Work;
