import React from "react";

import Background from "../common/Background";
import Buttons from "../common/Buttons";
import Heading from "../common/Heading";
import BasicStats from "./BasicStats";
import ImageCarousel from "./ImageCarousel";
import MusicWidget from "./MusicWidget";
import SearchTags from "./SearchTags";
import { infoBox } from "../../assets/data";

import { imageURLs } from "../../assets/data";
import "./AboutContainer.css";

const AboutContainer = () => {
  return (
    <div className="AboutContainer d-flex justify-content-center align-items-center font-primary m-3 mt-4 m-md-0 ">
      <div className="left d-none d-lg-block">
        <Background />
        <div
          style={{
            width: 35,
            height: 255,
            backgroundColor: "#f5f5f5",
            position: "absolute",
            bottom: 20,
            left: 20,
            border: "1px solid #e2e2e2",
          }}
        ></div>
      </div>
      <div className="right">
        <div
          className="d-flex justify-content-end mx-1 my-2"
          style={{ position: "relative" }}
        >
          <Buttons />
        </div>
        <div className="row" style={{ height: "95%", position: "absolute" }}>
          <div className="col-6">
            <div>
              <Heading
                inplace
                large
                first="tenwie"
                second="peng."
                style={{ position: "relative" }}
              />
            </div>
            <div className="font-primary subheading">
              software engineer w/ a passion for web development and design
            </div>
            <div>
              <ImageCarousel />
            </div>
            <div
              className="font-primary d-flex align-items-center"
              style={{ position: "absolute", bottom: 10, width: "110%" }}
            >
              <div className="circle-img">
                <img
                  className="my-face"
                  src={infoBox.imageURL}
                  alt="my face"
                  style={{
                    filter: "opacity(0.8)",
                    maxWidth: 100,
                    maxHeight: 75,
                  }}
                />
              </div>
              <div className="quote-wrapper">
                <div className="dark-tag">t.peng 彭天泳</div>
                <div className="quote">
                  "and remember that bad times... are just times that are bad."
                  Katrina
                </div>
              </div>
            </div>
          </div>
          <div className="col-6">
            <div>
              <MusicWidget />
            </div>
            <div className="hashtags">
              #gray #dimsum #rosemilktea #lisianthus
            </div>
            <div>
              <div className="d-flex justify-content-end pt-3">
                <img
                  src={imageURLs.design}
                  alt="line art"
                  style={{ height: 160 }}
                />
              </div>
            </div>
            <div>
              <SearchTags />
            </div>
            <div style={{ position: "absolute", bottom: 25, width: "90%" }}>
              <BasicStats />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutContainer;
