import React from "react";

import { imageURLs } from "../../assets/data";
import "./Buttons.css"

const Buttons = (props) => {
  return (
    <div className="Buttons">
      <button className="img-btn">
        <img src={imageURLs._} alt="_" />
      </button>
      <button className="img-btn">
        <img src={imageURLs.maximize} alt="◻" />
      </button>
      <button className="img-btn" onClick={props.xHandler}>
        <img src={imageURLs.x} alt="x" />
      </button>
    </div>
  );
};

export default Buttons;