import React, { Component } from "react";

import "./Deco.css";
import {imageURLs} from "../../assets/data"

class Deco extends Component {
  state = {
    hideImage: false,
  };

  render() {
    return (
      <div className="Deco d-inline-block px-1">
        <div className="d-flex justify-content-end my-2">
          <div className="exe">tenwie.exe</div>
          <button className="img-btn" onClick={() => this.setState({ hideImage: true })}>
              <img src={imageURLs._} alt="_" />
          </button>
          <button className="img-btn" onClick={() => this.setState({ hideImage: false })}>
              <img src={imageURLs.maximize} alt="◻" />
          </button>
          <button className="img-btn">
              <img src={imageURLs.x} alt="x" />
          </button>
        </div>
        <div className={`image-wrapper mb-4 ${this.state.hideImage ? "d-none" : ""}`}>
          <img
            src={imageURLs.pattern}
            alt="sidebar pattern"
            style={{ width: 200, height: 165, border: "1px solid #E5E5E5" }}
          />
        </div>
      </div>
    );
  }
}

export default Deco;
