import React from "react";

import Heading from "../common/Heading";
import "./BasicStats.css"

const BasicStats = () => {
  return (
    <div className="BasicStats" style={{ position: "relative" }}>
      <div className="d-flex justify-content-end">
        <Heading inplace reverse large first="basic" second="stats." />
      </div>
      <div
        style={{
          borderRight: "1px solid #E2E2E2",
          textAlign: "end",
          paddingRight: 15,
        }}
      >
        <div className="item">
          <span className="bold">name.</span> tenwie peng
        </div>
        <div className="item">
          <span className="bold">birth.</span> june
        </div>
        <div className="item">
          <span className="bold">pronouns.</span> she/her
        </div>
      </div>
    </div>
  );
};

export default BasicStats;
