import React from "react";

import { musicImgs } from "../../assets/data";
import "./MusicWidget.css";

const MusicWidget = () => {
  return (
    <div className="MusicWidget">
      <div className="music-player d-flex align-items-center flex-column">
        <div className="track d-flex justify-content-between align-items-center">
          <img src={musicImgs.shuffle} alt="shuffle" />
          <img src={musicImgs.trackPrev} alt="trackPrev" />
          <button className="pause-btn">
            <img src={musicImgs.pause} alt="pause" style={{ paddingTop: 2 }} />
          </button>
          <img src={musicImgs.trackNext} alt="trackNext" />
          <img src={musicImgs.repeat} alt="repeat" />
        </div>
        <div className="volume d-flex justify-content-between align-items-center">
          <img src={musicImgs.volumeDown} alt="volumeDown" style={{padding:"0 4px"}}/>
          <input
            id="volumeInput"
            type="range"
            min="0"
            max="100"
            value="50"
            onChange={() => {
              const slider = document.getElementById("volumeInput");
              const min = slider.min;
              const max = slider.max;
              const value = slider.value;

              slider.style.background = `linear-gradient(to right, #898989 0%, #898989 ${
                ((value - min) / (max - min)) * 100
              }%, #e2e2e2 ${
                ((value - min) / (max - min)) * 100
              }%, #e2e2e2 100%)`;
            }}
          />
          <img src={musicImgs.volumeUp} alt="volumeUp" />
        </div>
      </div>
      <div className="music-info d-flex align-items-center justify-content-between">
        <div className="d-flex align-items-center">
          <img
            className="album-img"
            src="https://twpeng-portfolio.s3-us-west-1.amazonaws.com/images/keshi-bandaids.jpg"
            alt="album art"
          />
        </div>
        <div className="song-info ">
          <div>NOW PLAYING</div>
          <div>bandaids - keshi</div>
        </div>
        <div className="dark-tag">Pause</div>
      </div>
    </div>
  );
};

export default MusicWidget;
